html,
body,
#app {
    height: 100%;
}

.full {
    width: 100%;
    height: 100%;
}

#map,
#next {
    height: 100%;
}

body {
    padding: 0;
    margin: 0;
}

html,
body {
    height: 100%;
}

.layout-warpper {
    height: 100%;
    width: 100%;
    display: table;
}

.layout-warpper__left {
    display: table-cell;
    width: 200px;
    border-right: 1px solid #ccc;
    height: 100%;
    /* 隐藏 */
    display: none;
}

.layout-warpper__container {
    display: table-cell;
    height: 100%;
}

.nav-menu {
    padding: 0 0 0 10px;
}

.nav-menu li {
    list-style: none;
    padding: 0;
    line-height: 150%;
}

.nav-menu li a {
    color: #333333;
    text-decoration: none;
}

.map-wrapper {
    width: 100%;
    height: 100%;
}

.example-page {
    padding: 10px;
    width: 720px;
    margin: auto;
}



